import { Component, OnInit } from '@angular/core';
import { User } from '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:string = '';
  password:string = '';
  tip_id:string = null;

  constructor(private restService:TipRestService,private router:Router,private activeRoute:ActivatedRoute) { }

  ngOnInit() {

	this.activeRoute.paramMap.subscribe(paramMap=>
	{
		console.log( paramMap );
	    if(paramMap.get('tip_id') )
	    {
	        this.tip_id= paramMap.get('tip_id');
	    }

		this.restService.getUser('me').subscribe(user=>{
			this.doNavigate();
		});
	});
  }

  doNavigate()
  {
		if( this.tip_id === null )
		{
			this.router.navigate(['/my-tips']);
		}
		else
		{
			this.router.navigate(['/buy',this.tip_id]);
		}
  }

  doLogin()
  {
		this.restService.doLogin(this.email,this.password).subscribe(user=>{
			this.doNavigate();	
			console.log("Login SUccess");
		});
  }
}
