import { Component, OnInit, Input } from '@angular/core';
import { Tip, TipInfo, League, Sport, Search } from '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import { switchMap } from 'rxjs/operators';
import { site_settings } from '../../models/tips';


@Component({
  selector: 'app-tips-list',
  templateUrl: './tips-list.component.html',
  styleUrls: ['./tips-list.component.css']
})

export class TipsListComponent implements OnInit {

  is_loading = true;
  tips: TipInfo[] = [];
  settings = site_settings;
  title = '';

  private _search;


  @Input()

  set search(search: Search) {
	this.title = search.title? search.title : '';
    this.restService.makeSearch(search).subscribe((tips_info) => {
      this.is_loading = false;
      this.tips = tips_info;
    });
  }

  constructor(private restService: TipRestService) {

  }

  ngOnInit() {

  }
}
