import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo,League,Sport } from  '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"
import { switchMap } from 'rxjs/operators';
import { site_settings } from  '../../models/tips';

@Component({
  selector: 'app-view-tip',
  templateUrl: './view-tip.component.html',
  styleUrls: ['./view-tip.component.css']
})
export class ViewTipComponent implements OnInit {

 
  constructor(private restService:TipRestService,private router:Router,private route:ActivatedRoute) { 
  }

  settings = site_settings;

  tip_info:TipInfo = null;
  is_loading:boolean = true;

  ngOnInit() {
		this.route.paramMap.pipe(
				switchMap((params: ParamMap) =>
				{
				   console.log( params );
				    let tip_id= 	params.get('tip_id')!=null ? parseInt(params.get('tip_id') ):null;

					return this.restService.getTipInfo( tip_id );
				}
		)).subscribe((tip_infos)=>
		{
			if( tip_infos.length == 0 )
			{
				return;
			}
			this.is_loading = false;
			this.tip_info = tip_infos[0];
		});
  }
}
