import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { TipRestService } from '../../services/tip-rest.service';
import { TipInfo} from '../../models/tips';

@Component({
  selector: 'app-tipster-offers',
  templateUrl: './tipster-offers.component.html',
  styleUrls: ['./tipster-offers.component.css']
})

export class TipsterOffersComponent implements OnInit {

  constructor(private restService:TipRestService, private router:Router) {}
  tip_infos:TipInfo[] = [];


  ngOnInit() {
		this.restService.makeSearch({ event_start: this.restService.getCurrentDateString(),authorized: false }).subscribe(tip_infos=>this.tip_infos = tip_infos);
  }
}
