import { Component, OnInit } from '@angular/core';
import { TipRestService } from '../../services/tip-rest.service';
import { Team,League,Tip,Sport,Image,User} from '../../models/tips';
import { FormGroup, FormControl } from '@angular/forms';
import { site_settings } from '../../models/tips';


@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {

  constructor(private restService:TipRestService) { 

  }

  is_loading:boolean = true;
  selectedFile:File = null;
  user:User = null;
  settings = site_settings;

  ngOnInit() {
	 this.restService.getUser('me').subscribe((user)=>
	 {
		this.user = user;
		this.is_loading = false;
	 });
  }



  save()
  {
	 this.restService.updateUser( this.user ).subscribe((user)=>
	 {
		this.user = user;
	 });
  }

  onFileSelected(event)
  {
	this.selectedFile = event.target.files[0];
	this.onUpload();
  }

  onUpload()
  {
	this.restService.uploadImage( this.selectedFile,false ).subscribe(image=>{
		this.user.image_id = image.id;
	});
  }
}
