import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo,League,Sport,User } from  '../../models/tips';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"
import { site_settings } from  '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';

//#region
declare function renderPaypalButtonSubscription(string,id):any;
//#endregion

@Component({
	selector: 'app-upgrade-pro',
	templateUrl: './upgrade-pro.component.html',
	styleUrls: ['./upgrade-pro.component.css']
})
export class UpgradeProComponent implements OnInit {

	constructor(private restService:TipRestService) { }

	user:User = null;
	is_loading = true;

	ngOnInit() 
	{
		this.restService.getUser('me').subscribe(user=>{
			this.user = user;
			renderPaypalButtonSubscription( site_settings.url_base, user );
			this.is_loading = false;
	    });
	}
}
