import { Component,Input, OnInit } from '@angular/core';
import { TipRestService } from '../../services/tip-rest.service';
import { TipInfo } from '../../models/tips';

@Component({
  selector: 'app-review-tip',
  templateUrl: './review-tip.component.html',
  styleUrls: ['./review-tip.component.css']
})
export class ReviewTipComponent implements OnInit {
   @Input() tip_info: TipInfo;

  constructor(private restService:TipRestService) { }

  ngOnInit() 
  {

  }
  
  onChangePredictionStatus(value)
  {
	 this.restService.updateTip({id: this.tip_info.tip.id, prediction_status: value }).subscribe(result=>
	 {
		this.tip_info.tip.prediction_status = value;
	 });
  }
}

