import { Component, OnInit } from '@angular/core';
import { TipRestService } from '../../services/tip-rest.service';
import { Sport } from '../../models/tips';

@Component({
  selector: 'app-add-sport',
  templateUrl: './add-sport.component.html',
  styleUrls: ['./add-sport.component.css']
})
export class AddSportComponent implements OnInit {

  constructor(private restService:TipRestService) { }

  name:string = '';
  ngOnInit() {
  }

  onCreateSport()
  {
		this.restService.addSport({name:this.name}).subscribe(sport=>{
				console.log("Created a new sport");
		});
  }
}
