import { Component, OnInit, HostBinding } from '@angular/core';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"
import { switchMap } from 'rxjs/operators';
import { TipRestService } from '../../services/tip-rest.service';
import { Sport,Team,Image } from '../../models/tips';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import { Location } from '@angular/common';
import { site_settings } from  '../../models/tips';

@Component({
  selector: 'app-edit-team',
  templateUrl: './edit-team.component.html',
  styleUrls: ['./edit-team.component.css']
})
export class EditTeamComponent implements OnInit {

  team:Team = null;
  is_loading:boolean=true;
  settings = site_settings;
  constructor(private restService:TipRestService,private router:Router,private route:ActivatedRoute,private location:Location) { 
  }

  ngOnInit() {
		this.route.paramMap.pipe(
				switchMap((params: ParamMap) =>
				{
				   console.log( params );
					let team_id = params.get('team_id')!=null ? parseInt(params.get('team_id') ):null;
					return this.restService.getTeam( team_id );
				}
		)).subscribe(team=>{ 
			this.is_loading = false;
			this.team= team;
		});
  }
  onSaveTeam()
  {
	this.restService.updateTeam( this.team ).subscribe((team)=>{
		setTimeout(()=>
		{
				this.location.back();	
		},1500);
	});
  }

  selectedFile:File = null;
  onFileSelected(event)
  {
	this.selectedFile = event.target.files[0];
	this.onUpload();
  }

  onUpload()
  {
	this.restService.uploadImage( this.selectedFile,false ).subscribe(image=>{
		this.team.image_id = image.id;
	});
  }

}

