import { Component, OnInit, HostBinding } from '@angular/core';
import { Router } from "@angular/router"
import { TipRestService } from '../../services/tip-rest.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  constructor(private tipService:TipRestService,private router:Router) { }
  @HostBinding('class')classes = 'create';

  name:string = '';
  email:string = '';
  password:string = '';
  password_confirmation:string = '';

  ngOnInit() {
		
  }

  createAccount()
  {
		this.tipService.createAccount({ name:this.name, email:this.email,password: this.password, password_confirmation: this.password_confirmation, type:'TIPSTER'}).subscribe(user=>
		{
			this.router.navigate(['/my-tips']);
		});
  }
}
