import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { League,User,Sport,Search} from '../models/tips';
import { Tip,TipInfo, Team,Image } from	'../models/tips';
import { site_settings } from '../models/tips';

@Injectable({ providedIn: 'root' })
export class TipRestService {

 private urlBase = site_settings.url_base;
 private user:User= null;

	constructor(private http: HttpClient) 
	{
		if(window.location.hostname == 'thepickzone.com' )
			this.urlBase = '/api';
	}

	addSport(sport:Sport):Observable<Sport>
	{
		return this.http.post<Sport>(`${this.urlBase}/sport.php`,sport,{ withCredentials: true });
	}

	buyFreeTip(tip_id):Observable<Tip>
	{
		return this.http.post<Tip>(`${this.urlBase}/buy.php`,{tip_id:tip_id},{ withCredentials: true });
	}

	getSports():Observable<Sport[]>
	{
		return this.http.get<Sport[]>( `${this.urlBase}/sport.php`);
	}

	getSport(sport_id):Observable<Sport>
	{
		return this.http.get<Sport>(`${this.urlBase}/sport.php?id=${sport_id}`);
	}

	buy_tip(tip_id:number):Observable<Tip>
	{
		return this.http.get<Tip>(`${this.urlBase}/buy.php?tip_id=${tip_id}`,{withCredentials: true});
	}

	getCurrentUser():User
	{
		return this.user;
	}

	getUser(user_id:string):Observable<User>
	{
		let result = this.http.get<User>( `${this.urlBase}/user.php?id=${user_id}`,{ withCredentials: true } );
		result.subscribe(user=>{
			if( user_id == 'me' )
			{
				console.log("INIT USER");
				this.user = user;
			}
		});
		return result;
	}

	getLeagues(sport_id:string):Observable<League[]>
	{
		return this.http.get<League[]>( `${this.urlBase}/league.php?sport_id=${sport_id}`);
	}

	getLeague(id:number):Observable<League>
	{
		return this.http.get<League>( `${this.urlBase}/league.php?id=${id}`);
	}

	addLeague(league:League):Observable<League>
	{
		return this.http.post<League>( `${this.urlBase}/league.php`,league, { withCredentials: true });
	}

	addTeam(team:Team)
	{
		return this.http.post<Team>( `${this.urlBase}/team.php`, team,{ withCredentials: true } );
	}

	getTeam(id:number):Observable<Team>
	{
		return this.http.get<Team>( `${this.urlBase}/team.php?id=${id}`);
	}

	getTeams(league_id:number):Observable<Team[]>
	{
		return this.http.get<Team[]>( `${this.urlBase}/team.php?league_id=${league_id}`,{ withCredentials: true });
	}
	updateLeague(league:League):Observable<League>
	{
		return this.http.put<League>(`${this.urlBase}/league.php?id=${league.id}`,league,{ withCredentials: true });
	}
	updateTeam(team:Team):Observable<Team>
	{
		return this.http.put<Team>(`${this.urlBase}/team.php?id=${team.id}`,team,{ withCredentials: true });
	}
	updateTip(tip:Tip):Observable<Tip>
	{
		return this.http.put<Tip>(`${this.urlBase}/tip.php?id=${tip.id}`,tip,{ withCredentials: true });
	}
	updateSport(sport:Sport):Observable<Sport>
	{
		return this.http.put<Sport>(`${this.urlBase}/sport.php?id=${sport.id}`,sport,{ withCredentials: true });
	}
	saveTip(tip:Tip):Observable<Tip>
	{
		if( tip.left_team_id == 0 )
			delete tip.left_team_id;
		if( tip.right_team_id == 0 )
			delete tip.right_team_id;

		return this.http.post<Tip>( `${this.urlBase}/tip.php`,tip, { withCredentials: true });
	}
	doLogin(email:string,password):Observable<User>
	{
		let result = this.http.post<User>( `${this.urlBase}/login.php`,{email,password}, { withCredentials: true });
		result.subscribe(user=>
		{
			this.user = user;
		});
		return result;
	}

	logout()
	{
		console.log("Login out");
		let result = this.http.get<any>( `${this.urlBase}/logout.php`,{ withCredentials: true });
		result.subscribe(result=>
		{
			this.user = null;
		});
		return result;
	}

	updateUser(user:User)
	{
		return this.http.put<any>( `${this.urlBase}/user.php`,user,{ withCredentials: true });
	}

	makeSearch(search:Search):Observable<TipInfo[]>
	{

		console.log("SEARCH OBJECT IS", search );
		let cp_search:Search = { };
		for(let z in search)
		{
			if( search[z] != null )
				cp_search[ z ] = search[z];
		}


		let params = new HttpParams();

		if( 'user_id'  in cp_search )
			params = params.append('user_id', ''+cp_search.user_id );

		if( 'sport_id' in cp_search )
			params =params.append('sport_id',''+cp_search.sport_id );

		if( 'team_id' in cp_search )
			params = params.append('team_id',''+cp_search.team_id);

		if( 'league_id' in cp_search )
			params = params.append('league_id',''+cp_search.league_id);

		if( 'event_start' in cp_search ) 
			params = params.append('event_start',''+cp_search.event_start);

		let authorized = 'authorized' in search ? search.authorized: false;
		
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{params: params, withCredentials: authorized });
		//return this.search(search.user_id, search.sport_id, search.team_id, search.league_id, search.team_name,search.event_start, search.authorized );
	}

	search(user_id,sport_id,team_id,league_id,team_name,start,authorized):Observable<TipInfo[]>
	{

		console.log("START IS ", start );
		return this.makeSearch({
			user_id: user_id,
			sport_id: sport_id,
			league_id: league_id,
			team_id: team_id,
			event_start: start,
			authorized: authorized
		});
	}
	getTipInfo(tip_id):Observable<TipInfo[]>
	{
		let params = new HttpParams();
		params = params.append('tip_id',tip_id);
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{params:params, withCredentials: true });
	}

	getTipsInfoByIds(tip_ids:number[]):Observable<TipInfo[]>
	{
		let params = new HttpParams();
		params = params.append('tip_id',tip_ids.join(','));
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{params:params, withCredentials: true });
	}
	

	getCurrentDateString():string
	{
		let zero = (z)=>{
			if( z < 10 )
				return '0'+z;
			return ''+z;
		};

		let date = new Date();
		return date.getUTCFullYear()+
						'-'+zero(date.getUTCMonth()+1)+
						'-'+zero(date.getUTCDate())+
						' '+zero(date.getUTCHours())+
						':'+zero(date.getUTCMinutes())+
						':'+zero(date.getUTCSeconds());
	}
	getTipInfoNexts():Observable<TipInfo[]>
	{
		return this.getTipsInfoByEventDate( this.getCurrentDateString() );
	}

	getTipsInfoByEventDate(date:string):Observable<TipInfo[]>
	{
		let params = new HttpParams();
		params = params.append('event_start',date);
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{params:params, withCredentials: true });
	}
	
	getTipsInfoPending()
	{
		let params = new HttpParams();
		params = params.append('prediction_status','PENDING');
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{params:params, withCredentials: true });
	}

	getTipsInfo():Observable<TipInfo[]>
	{
		return this.http.get<TipInfo[]>( `${this.urlBase}/tip_info.php`,{ withCredentials: true });
	}

	createAccount(user:User):Observable<User>
	{
		return this.http.post<User>( `${this.urlBase}/user.php`,user,{ withCredentials: true });
	}
	uploadImage(file:File,is_private:boolean):Observable<Image>
	{
		let fd = new FormData();
		fd.append('image',file, file.name);
		fd.append('is_private', is_private?'1':'0' );
		return this.http.post(`${this.urlBase}/image.php`,fd,{withCredentials:true});
	}

	//uploadFile(file:File):Observable<FileInfo>
	//{
	//	let fd = new FormData();
	//	fd.append('image',file, file.name);
	//	return this.http.post.('url',fd );
	//}
}
