import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo,League,Sport } from  '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"
import { switchMap,flatMap,mergeMap,catchError,filter } from 'rxjs/operators';
import { from,of } from 'rxjs';
import { site_settings } from  '../../models/tips';

//#region
declare function renderPaypalButton(url,id):any;
declare function renderPaypalButton2(url,id):any;
//#endregion

@Component({
  selector: 'app-buy-tip',
  templateUrl: './buy-tip.component.html',
  styleUrls: ['./buy-tip.component.css']
})
export class BuyTipComponent implements OnInit {
  
  constructor(private restService:TipRestService,private router:Router,private route:ActivatedRoute) { 

  }

  is_loading:boolean = true;
  tip_info:TipInfo = null;
  tip_id: number = 0;
  error:string = null;

  ngOnInit() 
  {
		this.route.paramMap.pipe
		(
			switchMap
			(
			    paramMap=>{
					console.log("FOOO");
					this.tip_id	= 	paramMap.get('tip_id')!=null ? parseInt(paramMap.get('tip_id') ):0;
					return this.restService.getUser('me').pipe
					(
						catchError(error => 
						{
							return of(null);
						})
					);
				}
			),
			switchMap(user=>
			{
				if( user == null )
				{
					this.router.navigate(['/login-redirect',this.tip_id]);
					return from(null);
				}

				console.log("GETTING tip info");
				return this.restService.getTipInfo( this.tip_id ).pipe
				(
					catchError( error =>
					{
						console.log("erro on getTipInfo",error);
						return from( null );
					})
				);
			})
			,filter( tip_infos=> {
				console.log("FIltering tip_info",tip_infos);
				return tip_infos !== null 
			})
		)
		.subscribe
		(
			tip_infos=>
			{
				console.log("ON SUBSCRIBER",tip_infos);
				this.is_loading = false;	
				this.tip_info = tip_infos[0];

				let date = null;
				let zero = (z)=>{
					if( z < 0 )
						return '0'+z;
					return ''+z;
				};

				let date_str = '';

				let tipDate	= new Date( this.tip_info.tip.event_start );
				let currentDate =new Date();

				if('bought' in this.tip_info && this.tip_info.bought == 1 )
				{
				    console.log("Tip is bought");
					this.router.navigate(['/view-tip/',this.tip_info.tip.id]);
				}
				else if( this.tip_info.tip.price == 0 )
				{
					console.log("Price is 0 do nothing");	
				}
				else if(  tipDate > currentDate )
				{
					renderPaypalButton2( site_settings.url_base, this.tip_info.tip );
				}
				console.log("Is loading false");
			}
			,error=>
			{
				console.log("error catched on the subscriber",error);
			}
		);
	}

	buyTip()
	{
		this.restService.buyFreeTip( this.tip_id ).subscribe((tip)=>
		{
			this.router.navigate(['/view-tip/',this.tip_id]);
		});
	}
}
