import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo,League,Sport } from  '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import {Router,ActivatedRoute,ParamMap} from "@angular/router"
import { switchMap } from 'rxjs/operators';
import { site_settings } from  '../../models/tips';

@Component({
  selector: 'app-edit-leagues',
  templateUrl: './edit-leagues.component.html',
  styleUrls: ['./edit-leagues.component.css']
})
export class EditLeaguesComponent implements OnInit {

  constructor(private restService:TipRestService,private router:Router,private route:ActivatedRoute) { 

  }

  sport_id:string = 'Soccer';
  leagues:League[] = [];
  sports:Sport[] = [];
  is_loading:boolean = true;
  settings = site_settings;

  ngOnInit() {
	this.onSportChange( this.sport_id );
	this.restService.getSports().subscribe((sports)=>
	{
		this.sports = sports;
	});
  }

	onSportChange(sport_id:string)
	{
		this.is_loading = true;
		this.sport_id = sport_id;
		this.restService.getLeagues(sport_id).subscribe((leagues)=>
		{
			this.leagues = leagues;
			this.is_loading = false;
		});
	}
}
