import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo,League,Sport,Team } from  '../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';
import {Router,ActivatedRoute,ParamMap} from "@angular/router";
import { switchMap } from 'rxjs/operators';
import { site_settings } from  '../../models/tips';

@Component({
  selector: 'app-edit-league',
  templateUrl: './edit-league.component.html',
  styleUrls: ['./edit-league.component.css']
})
export class EditLeagueComponent implements OnInit {

  league:League = null;
  is_loading:boolean = true;
  teams:Team[] = [];
  success:string = null;
  settings = site_settings;

  constructor(private restService:TipRestService,private router:Router,private route:ActivatedRoute) { 
  }

  ngOnInit() {
		this.route.paramMap.pipe(
				switchMap((params: ParamMap) =>
				{
				   console.log( params );
					let league_id = parseInt( params.get('league_id') );
					this.restService.getTeams(league_id ).subscribe((teams)=>
				    {
						this.teams = teams;
					});
					return this.restService.getLeague( league_id );
				}
		)).subscribe(league=>{ 
				this.league = league;
				this.is_loading = false;
		console.log("Is loading false");
		});
  }

  updateLeague()
  {
	this.restService.updateLeague( this.league ).subscribe((league)=>
    {
		this.success = "league data updated"
		setTimeout(()=>{
				this.league = league;
				this.success = null;	
		},1500);	
	});
  }

  selectedFile:File = null;
  onFileSelected(event)
  {
	this.selectedFile = event.target.files[0];
	this.onUpload();
  }

  onUpload()
  {
	this.restService.uploadImage( this.selectedFile,false ).subscribe(image=>{
		this.league.image_id = image.id;
	});
  }
}
