import { Component, OnInit } from '@angular/core';
import { Tip,TipInfo } from	'../../models/tips';
import { TipRestService } from '../../services/tip-rest.service';

@Component({
	selector: 'app-review-tip-list',
	templateUrl: './review-tip-list.component.html',
	styleUrls: ['./review-tip-list.component.css']
})
export class ReviewTipListComponent implements OnInit {

	tips:TipInfo[] = [];
	dateFilter:string = '';
	event_start:string = '';


	constructor(private tipService:TipRestService) { }

	ngOnInit() {
			this.tipService.getTipsInfoPending().subscribe(tip_infos=>this.tips = tip_infos );
	}

	onChangeFilter(value)
	{
		this.dateFilter = value;
		let date = null;

		let zero = (z)=>{
			if( z < 0 )
				return '0'+z;
			return ''+z;
		};

		let date_str = '';

		if( value !== 'PENDING' )
		{
			date = new Date(this.event_start+' 00:00:00');

			console.log( date );

			date_str = date.getUTCFullYear()+
							'-'+zero(date.getUTCMonth()+1)+
							'-'+zero(date.getUTCDate())+
							' '+zero(date.getUTCHours())+
							':'+zero(date.getUTCMinutes())+
							':'+zero(date.getUTCSeconds());
		}

		switch( value )
		{
			case 'PENDING': this.tipService.getTipsInfoPending().subscribe(tip_infos=>this.tips = tip_infos );	break;
			case 'EVENT_DATE': this.tipService.getTipsInfoByEventDate(date_str).subscribe(tip_infos=>this.tips = tip_infos );	break;
			//case 'REVIEW_DATE': this.tipService.getTipsInfoByReviewDate(date_str).subscribe(tip_infos=>this.tips = tip_infos );	break;
		}
	}
}
